<template>
    <div>
      <van-nav-bar title="订单结算"  left-arrow @click-left="$router.replace('/cart')" fixed z-index="100"  placeholder :border="false" />
      <div>
         <div class="contact-card" @click="$router.push('/addressList')">
           <div class="left" v-if="!defaultAddress">
            <van-icon name="plus" size="20" class="icon-plus" />
            <div class="text">选择收货地址</div>
           </div>
           <div class="left" v-else>
            <van-icon name="logistics" class="icon-logistics" />
            <div class="user-info">
                <div class="top">{{ defaultAddress.name + ' ' + defaultAddress.phone }}</div>
                <div class="bot line">{{ defaultAddress.region.province + defaultAddress.region.city + defaultAddress.region.region + defaultAddress.detail }}</div>
            </div>
           </div>
           <van-icon name="arrow" class="icon-arrow" />
         </div>
         <div class="content">
          <div class="goods-list">
            <div class="item" v-for="item in order.goodsList" :key="item.goods_id">
              <img :src="item.goods_image" alt="" class="goods-img">
              <div class="right">
                <div class="title line2">{{ item.goods_name }}</div>
                <div class="bot">
                  <div class="num">x{{ item.total_num }}</div>
                  <div class="price">￥{{ item.goods_price_min }}</div>
                </div>
              </div>
            </div>
            <div class="total">共{{order.orderTotalNum}}件商品，合计：<span>￥{{ order.orderTotalPrice }}</span></div>
          </div>
          <div class="info">
            <div class="item">
              <div class="label">订单总金额：</div>
              <div class="text"><span>￥{{ order.orderTotalPrice }}</span></div>
            </div>
            <div class="item">
              <div class="label">优惠券：</div>
              <div class="text" v-if="couponList.length===0">无优惠券可用</div>
              <div class="link" v-else @click="isShowCoupon = true">
                <div class="text" v-if="selCoupon.price === 0">有{{ couponList.length }}张优惠券</div>
                <div class="text" v-else>-￥{{ selCoupon.price }}</div>
                <van-icon name="arrow" color="#999" />
              </div>
            </div>
            <div class="item">
              <div class="label">配送费用：</div>
              <div class="text"><span>+￥0.00</span></div>
            </div>
          </div>
          <div class="payType">
            <div class="title">支付方式</div>
            <div class="list">
              <div class="item">
                <div class="left">
                  <van-icon name="balance-o" size="24" />
                  <div class="text">余额支付（可用￥{{personal.balance}}元）</div>
                </div>
                <van-icon name="passed"  color="#EE0A24" size="22" />
              </div>
            </div>
          </div>
          <div class="remark">
            <textarea v-model="remark" class="text" placeholder="选填：买家留言（50字内）" maxlength="50"></textarea>
          </div>
         </div>
         <div class="bot-box">
          <div class="left">实付款：<span>￥{{ orderPayPrice }}</span></div>
          <button class="btn" @click="submit">提交订单</button>
         </div>
      </div>
      <van-action-sheet v-model="isShowCoupon" title="选择优惠券" :closeable="false" class="coupon-mask">
        <div class="content">
          <div class="coupon-list">
            <coupon-item :coupon="item" :index="index" type="coupon3" v-for="(item, index) in couponList" :key="item.user_coupon_id" @couponClick="couponClick"></coupon-item>
          </div>
          <button class="btn" @click="noCoupon">不使用优惠券</button>
        </div>
      </van-action-sheet>
    </div>
</template>

<script>
import CouponItem from '@/components/CouponItem.vue'
export default {
  name: 'PayIndex',
  components: {
    CouponItem
  },
  data () {
    return {
      order: {},
      personal: {},
      couponList: [],
      remark: '',
      isShowCoupon: false,
      selCoupon: {
        price: 0,
        id: 0
      }
    }
  },
  computed: {
    defaultAddress () {
      return this.$store.getters['address/defaultItem'] || ''
    },
    orderPayPrice () {
      const price = this.order.orderTotalPrice - this.selCoupon.price
      return price.toFixed(2)
    },
    mode () {
      return this.$route.query.mode
    },
    obj () {
      if (this.mode === 'cart') {
        return { cartIds: this.$route.query.cartIds }
      } else {
        return {
          goodsId: this.$route.query.goodsId,
          goodsNum: this.$route.query.goodsNum,
          goodsSkuId: this.$route.query.goodsSkuId
        }
      }
    }
  },
  created () {
    this.$store.dispatch('address/getAddressList')
    this.getOrderList()
  },
  methods: {
    async getOrderList () {
      const { data: { order, personal } } = await this.$api.order.checkOrder(this.mode, this.obj)
      this.order = order
      this.personal = personal
      this.couponList = order.couponList
    },
    async submit () {
      await this.$api.order.submitOrder(this.mode, this.obj, this.remark, this.selCoupon.id)
      this.$toast.success('支付成功')
      this.$router.replace('/order?dataType=all')
    },
    couponClick (coupon) {
      this.selCoupon = coupon
      this.isShowCoupon = false
    },
    noCoupon () {
      this.selCoupon = {
        price: 0,
        id: 0
      }
      this.isShowCoupon = false
    }
  }
}
</script>

<style lang="less" scoped>
.contact-card{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
  border-bottom: 5px solid;
  border-image: url(../../assets/images/border.png);
  border-image-slice: 0 0 40 0;
  border-image-width:5px;
  border-image-repeat: round repeat;
  .left{
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    .user-info{
      width: 88%;
      margin-left: 15px;
      font-size: 16px;
    }
    .icon-plus{
      font-weight: bold;
    }
    >.text{
      font-size: 16px;
      margin-left: 10px;
    }
  }
}
.content{
  padding: 0 15px 50px;
  .goods-list{
    padding: 10px 0;
    border-bottom: 1px solid #f3f1f1;
    .item{
      display: flex;
      align-items: center;
      justify-content: flex-start;
      padding: 10px 0;
      .goods-img{
        width: 90px;
        height: 90px;
      }
      .right{
        flex: 1;
        margin-left: 15px;
        height: 90px;
        .title{
          font-size: 15px;
        }
        .bot{
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-top: 8px;
          .num{
            font-size: 14px;
          }
          .price{
            font-size: 15px;
            color: orangered;
          }
        }
      }
    }
    .total{
      text-align: right;
      font-size: 15px;
      span{
        color: orangered;
      }
    }
  }
  .info{
    padding: 2px 0;
    border-bottom: 1px solid #f3f1f1;
    .item{
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: 15px;
      padding: 8px 0;
      span{
        color: orangered;
      }
      .link{
        display: flex;
        align-items: center;
        .text{
          color: orangered;
          margin-right: 5px;
        }
      }
    }
  }
  .payType{
    padding: 10px 5px 8px 0;
    border-bottom: 1px solid #f3f1f1;
    .title{
      font-size: 15px;
      margin-bottom: 5px;
    }
    .list{
      .item{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 8px 0;
        .left{
          display: flex;
          align-items: center;
          justify-content: flex-start;
          .text{
            margin-left: 8px;
            font-size: 15px;
          }
        }
      }
    }
  }
  .remark{
    padding: 12px 0;
    .text{
      width: 100%;
      min-height: 100px;
      font-size: 15px;
    }
  }
}
.bot-box{
  border-top: 1px solid #f3f1f1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 50px;
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 10;
  background-color: #fff;
  .left{
    font-size: 16px;
    padding-left: 15px;
    span{
      color: orangered;
    }
  }
  .btn{
    color: #fff;
    font-size: 14px;
    padding: 0 35px;
    height: 100%;
    background: linear-gradient(to right, #ee0a24, #ff6034,);
  }
}
.coupon-mask{
  .content{
    padding:0 20px 30px;
    .coupon-list{
      min-height: 300px;
    }
    .btn{
      display: block;
        width: 90%;
        height: 36px;
        color: #888;
        border: 1px solid #e3e3e3;
        border-radius: 5px;
        font-size: 14px;
        margin: 0 auto;
    }
  }
}
</style>
